const Inviting = ()=>{

    const {state,dispatch} = useWeb3();
    const { maincontract,Account} = state;

    const [entry, setEntry] = useState('');
    const [showTips, setShowTips] = useState(false);

    const handleChange = async(e) =>{
        setEntry(e.target.value);
        console.log("====.target.value.length==",e.target.value.length)
        if(e.target.value.length === 32){
            dispatch({type: 'SET_LOADING',payload:{
                    tips:" Loading...",
                }});
             await services.isCodeUsed(maincontract,e.target.value,Account).then(data=>{
                console.log("=====isCodeUsed=====",data)
                setShowTips(!!data);
                dispatch({type: 'SET_LOADING',payload:null});
            });
        }

    }
    const submitValue = (e) =>{
        if (e.keyCode === 13) {
           submit()
        }

    }
    const toTelegram = ()=>{
        window.open("https://t.me/ZeroHybrid_Bot");
    }
    const submit = () =>{
        const codeArr = plan.records.filter(item=>item.code === entry);

        if(!codeArr.length){
            setShowTips(true);
            return;
        }else{
            setShowTips(false);
        }

        let obj = {
            codeBefore:codeArr[0].code,
            proofBefore:codeArr[0].proof
        };

        dispatch({type: 'SET_LOADING',payload:{
                tips:"Register ...",
            }});
        services.register(maincontract, obj, Account).then((data)=>{
            dispatch({type: 'SET_LOADING',payload:null});
            console.log(data);
            dispatch({type: 'SET_INVITE',payload:null});
        }).catch(err=>{
            dispatch({type: 'SET_LOADING',payload:null});
            setShowTips(true);
            console.log(err)
        })
    }
    useEffect(()=>{

        const checkAccount = async () =>{
            let ret = await services.isAccountRegisterred(maincontract,Account);
            if(ret){
                dispatch({type: 'SET_INVITE',payload:null});
            }
        }
        checkAccount();
    },[])

    return <MskBg>
        <BgLayer>
            <Titles>Ready to participate in the ZeroHybrid Staking Event? </Titles>

            <Main>
                <InputBrdr type="text" value={entry}  onChange={(e)=>handleChange(e)} onKeyDown={submitValue} placeholder="Enter your Exclusive Entry Code "/>
                <button onClick={submit} disabled={!(entry.length || showTips)}> Enter</button>

            </Main>
            {
                showTips &&  <Tips>* Wrong invitation code, please try again</Tips>
            }

            <Contents>If you don’t have one, please add @ZeroHybrid_Bot via telegram and send command /sendcode to get your exclusive entry code.</Contents>
            <LinkRht onClick={toTelegram}>
                <Btm>Go to Telegram <img src={arrow} alt=""/></Btm>
            </LinkRht>

        </BgLayer>

    </MskBg>;
};
export default  Inviting;
